import data from '../data';
import {filterList, getFilterList, sortUp, sortDown, getPageView, makeBasket, incBasketItem, decBasketItem, removeBasketItem, clearFilter} from './first';
data.view=[...data.tea];
sortUp(data.view, 'price');
data.sortUp = true;
data.page = 1;
data.pageView = [...data.view.slice(0, 6)];
data.basket=[];
const initState = data;

const reducer = (state=initState, action) => {
    switch(action.type){
        case "UPDATE_CATEGORY":
            const new_category = [...state.category.list.slice(0, action.payload), 
                              {title:state.category.list[action.payload].title, filter: !state.category.list[action.payload].filter},
                              ...state.category.list.slice(action.payload+1)
                             ]
            return {...state,
                    category: {...state.category, list:new_category}         
                  }
        case "UPDATE_COUNTRY":
            const new_country = [...state.country.list.slice(0, action.payload), 
                              {title:state.country.list[action.payload].title, filter: !state.country.list[action.payload].filter},
                              ...state.country.list.slice(action.payload+1)
                             ]
            return {...state,
                   country: {...state.country, list:new_country}         
                  }
        case "SET_FILTER_LIST":
            const x = getFilterList(state.tea, filterList, state.category);
            const y = getFilterList(x, filterList, state.country);
            if(state.sortUp){sortUp(y, 'price')}else{sortDown(y, 'price')}
            return {...state, view: y, pageView: [...y.slice(0, 6)]};
			
			
		case "CLEAR_FILTER_LIST":
			const newListCategory = clearFilter(state.category.list);
			const newListCountry = clearFilter(state.country.list);
			let view = [...state.tea];
			if(state.sortUp){sortUp(view, 'price')}else{sortDown(view, 'price')};
			console.log(view);
			
			return{...state,
			       categoty: {...state.category, list: newListCategory},
				   country: {...state.country, list: newListCountry}, 
				   page: 1, 
				   view,
				   pageView: getPageView(view, 1)				   
				   }
			
        case "SORT_UP":
            const viewUp = [...state.view];
            sortUp(viewUp, 'price')
            return {...state, view: viewUp, sortUp: true, pageView: [...viewUp.slice(0, 6)]}
        case "SORT_DOWN":
            const viewDown = [...state.view];
            sortDown(viewDown, 'price');
            return {...state, view: viewDown, sortUp: false, pageView: [...viewDown.slice(0, 6)]}
        case "SET_PAGE":
            return{...state, page: action.payload, pageView: getPageView(state.view, action.payload)};
		case "ADD_TO_BASKET":
		    const newBasket = makeBasket(state.basket, action.payload);
            return{...state, basket: newBasket};
        case "INC_BASKET_ITEM":
            const newBasketInc = incBasketItem(state.basket, action.payload);
            return{...state, basket: newBasketInc};
        case "DEC_BASKET_ITEM":
            const newBasketDec = decBasketItem(state.basket, action.payload);
            return{...state, basket: newBasketDec};
        case "REMOVE_BASKET_ITEM":
            const newBasketRemove = removeBasketItem(state.basket, action.payload);
            return{...state, basket: newBasketRemove};
        default: 
            return state;
    }
}

export default reducer;