const filterList = (list, filter='filter', title='title') => {
    let filterTrue = list.filter((item)=>{
        return item[filter]===true;
    });
    let arr = filterTrue.map((item)=>{
        return item[title]
    });
    return arr;
}

const getFilterList = (list, func, config) => {
    const arr = func(config.list);
    if(arr.length===0)return list;
    let newArr = [];
    
    arr.map((filterText)=>{
        let arr1 = list.filter((item)=>{
            return item[config.fieldName] === filterText;

        })
        newArr=[...newArr, ...arr1]
    })
    return newArr
}

const clearFilter = (list, filter='filter', title='title') => {
	const newList = list.map((item)=>{
		if(item[filter]===true){
			item[filter] = false;
			return item;
		}else{
			return item;
		};
	});
	return newList;
};

const sortUp = (list, price) => {
    list.sort((a, b) => a[price] > b[price] ? 1 : -1)
}

const sortDown = (list, price) => {
    list.sort((a, b) => a[price] > b[price] ? -1 : 1)
}

const getPageView = (list, num)=>{
    if(num===1){return list.slice(0, 6)}
    return list.slice(num*6-6, num*6)
}

const makeBasket = (arr, item) => {
	let newArr=[];
    if(arr.length===0){newArr=[item]; 
        return newArr}
	
	arr.map((el)=>{
		if(el.id === item.id){
			let massa = el.massa+item.massa;
			if(massa>1000){massa=1000}
			newArr.push({...el, massa: massa});
		}else{
			newArr.push(el);
		}
	});
	const x = newArr.filter((el)=>{return el.id===item.id});
	if(x.length===0){newArr.push(item)}
	return newArr;
}

const incBasketItem = (arr, id) =>{
    return arr.map((item)=>{
        if(item.id===id){
            let massa = item.massa+100;
            if(massa>1000){massa=1000}
            return{...item, massa}
        }else{return item}
    });
};

const decBasketItem = (arr, id) =>{
    return arr.map((item)=>{
        if(item.id===id){
            let massa = item.massa-100;
            if(massa<100){massa=100};
            return{...item, massa};
        }else{return item};
    });
};

const removeBasketItem = (arr, id) => {
    return arr.filter((item)=>{
        return item.id !== id;
    });
}

export {filterList, getFilterList, sortUp, sortDown, getPageView, makeBasket, incBasketItem, decBasketItem, removeBasketItem, clearFilter};
