import React from 'react';
import {connect} from 'react-redux';
import TeaListItem from '../tea-list-item';
import './tea-list.css';

const TeaList = (props)=>{
    
    const listTeaElements = props.listTea.map((item)=>{
        return <div key={item.id} className="card"><TeaListItem item={item} /></div>
    })
    
    return(
    <div className="tea-list">
        {listTeaElements}
    </div>
    )
}

const mapStateToProps = (state)=>{
    return{listTea: state.pageView}
}



export default connect(mapStateToProps)(TeaList);