import React from 'react';
import ReactDOM from 'react-dom';
import {Provider} from 'react-redux';
import {HashRouter as Router} from 'react-router-dom';
import store from './store';
import ErrorBoundery from './components/error-boundery';
import TeaService from './services/teashop-services';
import {TeaShopProvider} from './components/teashop-service-context';
import App from './components/app';

const teashopServices = new TeaService();

ReactDOM.render(
                  <Provider store={store}>
                    <ErrorBoundery>
                      <TeaShopProvider value={teashopServices}> 
                        <Router>
                          <App />
                        </Router>  
                      </TeaShopProvider>
                    </ErrorBoundery>
                  </Provider>, document.getElementById('root'));