import React from 'react';
import {connect} from 'react-redux';
import TeaListItem from '../tea-list-item';
import './most-popular.css';

const MostPopular = (props) => {
    return(
    <div className="most-popular">
    <h1>Самое популярное</h1>
    <div className="flex">
      <TeaListItem item={props.teaList[0]}/>    
      <TeaListItem item={props.teaList[4]}/>  
      <TeaListItem item={props.teaList[8]}/>
      <TeaListItem item={props.teaList[11]}/>
    </div>
    </div>
    );
}

const mapStateToProps = (state) => {//тестовый функционал, нужно почистить 
	return{teaList: state.tea}
}

export default connect(mapStateToProps)(MostPopular);