import React from 'react';
import {Link} from 'react-router-dom';
import './header.css';

import {ReactComponent as Menu} from '../svg/menu.svg';
import {ReactComponent as CloseMenu} from '../svg/close.svg';
import {ReactComponent as Basket} from '../svg/basket.svg';



class Header extends React.Component {

  constructor(props){
    super(props);
    this.state = {isShowMenu: false}
  }

  toggleMenu = () => {
    this.setState((state)=>{
      document.querySelector('html').classList.toggle('no-scroll');
      return{isShowMenu:!state.isShowMenu}});
  }
    
  render(){

    let toggleShowMenu = this.state.isShowMenu?'nav-show':'';
    let iconMenu = this.state.isShowMenu?<CloseMenu />:<Menu />

    return(
      <header className="header">
          <div className="nav-icon" onClick={this.toggleMenu}>{iconMenu}</div>
          <div className="logo"><Link to="/"><p>CupTea</p></Link></div>
          <ul className={toggleShowMenu}>
            <li><Link onClick={this.toggleMenu} to="/">Главная</Link><span></span></li>
            <li><Link onClick={this.toggleMenu} to="/catalog">Каталог</Link><span></span></li>
            <li><Link onClick={this.toggleMenu} to="/delivery">Доставка</Link><span></span></li>
            <li><Link onClick={this.toggleMenu} to="/contacts">Контакты</Link><span></span></li>
          </ul>      
          <div className="basket"><Link to="/basket/"><Basket /></Link></div>
      </header>
    );
  }
}

export default Header;