import React from 'react';
import StockItem from './stock-item';
import './stock.css';

const Stock = () => {
    return(
    <div className="stock">
        <div className="stock-block">
            <h1>Акции</h1>
            <div className="flex">
                <StockItem title="В этом месяце" text="скидка всем зарегистрированным клиентам 10%" />    
                <StockItem title="Бесплатная доставка" text="на сумму заказа от 3000 руб" />    
                <StockItem title="Подарок" text="к каждому десятому заказу дарим подарок" />    
                <StockItem title="Оптовым покупателям" text="бесплатно предоставляем набор из всех видов чая" />    
            </div>
        </div>
    </div>
    );
};

export default Stock;