import React, {Component} from 'react';
import {withRouter} from 'react-router-dom';
import {connect} from 'react-redux';
import {DetailMain} from '../detail-item';
import './detail.css';

class Detail extends Component{

    
    render(){
        window.scrollTo(0, 0);
        const {id} = this.props.match.params;
        const item = this.props.teaList.filter((item)=>{
            return item.id==id;
        })[0];
        return(
            <div className="detail">
                <div className="detail-image">
                    <img src="https://artlabar.github.io/cup-tea/img/banner_goods.jpg" alt="fon" />
                </div>
                <div className="detail__box">
                    <DetailMain item={item} />
                </div>
            </div>
        );
    }
}

const mapStateToProps=(state)=>{
    return{teaList: state.view}
}

export default withRouter(connect(mapStateToProps)(Detail));