import React, {Component} from 'react';
import {connect} from 'react-redux';
import FilterList from './filter-list';
import {updateCotegory, updateCountry, setFilterList, clearFilterList} from '../../actions';
import './filter.css';

class Filter extends Component{

    isToggleShow = () => {
        document.querySelector('.is-view').classList.toggle('filter-show');
    }

    render(){

    return(
        <div className="filter">
            <div className="filter-toggle" onClick={this.isToggleShow}>
                <p>Фильтр по категориям:</p>
            </div>
            <div className="is-view">
                <FilterList config={this.props.category} update={this.props.updateCotegory} />
                <FilterList config={this.props.country} update={this.props.updateCountry} />
                <button className="add-filter" 
                        onClick={this.props.setFilterList}>
                    Применить
                </button>
                <button className="remove-filter" 
                        onClick={this.props.clearFilterList}>
                    Сбросить
                </button>
            </div>
        </div>
    );}
}

const mapStateToProps = (state)=>{
    return {country: state.country, category: state.category}
}

const mapDispatchToProps = (dispatch) => {
    return({setFilterList: ()=>{dispatch(setFilterList())},
            updateCotegory: (idx)=>{dispatch(updateCotegory(idx))},
            updateCountry: (idx)=>{dispatch(updateCountry(idx))},
    		clearFilterList: ()=>{dispatch(clearFilterList())}
            });
}

export default connect(mapStateToProps, mapDispatchToProps)(Filter);