import React from 'react';
import {connect} from 'react-redux';
import {incBasketItem, decBasketItem, removeBasketItem} from '../../actions';
import {BasketItem, TotalOrder} from '../basket-item';
import './basket.css';

const Basket = (props) => {


    const basketItems = props.basketList.map((item)=>{
        return <div key={item.id}>
                   <BasketItem removeBasketItem={props.removeBasketItem} 
                               item={item} 
                               incBasketItem={props.incBasketItem} 
                               decBasketItem={props.decBasketItem} />
               </div>
    });

    const sumCost = (basketList) => {
        let sum = 0;
        basketList.forEach((item)=>{
            sum = sum+item.massa/100*item.price;
        });
        return sum;
    }

    return(
        <div className="basket-page">
            <div className="basket-header">Ваша корзина</div>
              <div className="basket-flex">
                <div className="basket-flex-header">
                  <p className="basket-flex-header__price">Цена</p>
                  <p className="basket-flex-header__count">Количество</p>
                  <p className="basket-flex-header__sum">Сумма</p>
                </div>
                <div className="flex-basket-item">{basketItems}</div>
                <div className="flex-total-price" ><TotalOrder total={sumCost(props.basketList)} /></div>
            </div>            
        </div>
    );
}

const mapStateToProps = (state) => {
    return {basketList: state.basket}
}

const mapDispatchToProps = (dispatch) => {
    return{
        incBasketItem: (id)=>{dispatch(incBasketItem(id))},
        decBasketItem: (id)=>{dispatch(decBasketItem(id))},
        removeBasketItem: (id)=>{dispatch(removeBasketItem(id))}
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Basket);