import React, {Component} from 'react';
import {connect} from 'react-redux';
import {Link} from 'react-router-dom';
import {addToBasket} from '../../actions';
import './tea-list-item.css';

class TeaListItem extends Component{


    makeItemForBasket = (item, massa=100)=>{
    return{id: item.id, title: item.title, massa, price: item.price, image: item.imageUrl, country: item.country, category: item.categoru}
  };
  
    toggleTextButton = (evt) => {
      const el =  evt.target;
      el.classList.add('tttt');
      const text = evt.target.innerHTML;
      evt.target.innerHTML = "ДОБАВЛЕНО";
      setTimeout(()=>{el.innerHTML=text; el.classList.remove('tttt')}, 1000);
    }
    
    render(){
        const {title, categoru, imageUrl, id, price} = this.props.item;
    return(
    <div className="tea-list-item">
      <Link to={`/details/${id}`}>
      <img src={imageUrl} alt="tea-item" />
      <p className="tea-name">{title}</p>
      <p className="tea-category">{categoru}</p>
      <p className="tea-price">{price} руб.</p>
      </Link>
      <button className="add-basket" onClick={(evt)=>{this.props.addToBasket(this.makeItemForBasket(this.props.item)); this.toggleTextButton(evt)}}>В КОРЗИНУ</button>
      <span></span>
    
    </div>
    )
    }
}

const mapStateToProps = (state) => {//тестовый функционал, нужно почистить 
	return{data: state}
}

const mapDispatchToProps = (dispatch) => {
	return{addToBasket: (id, massa)=>{dispatch(addToBasket(id, massa))}}
}

export default connect(mapStateToProps, mapDispatchToProps)(TeaListItem);