import React, {Component} from 'react';
import {connect} from 'react-redux';
import {addToBasket} from '../../actions';
import {DetailMore} from '../detail-item';
import './detail-main.css';

class DetailMain extends Component{
    constructor(){
        super();
        this.state={
            priceConst: null,   
            massa: 100,
            sumPrice: null
        }
    }

    componentDidMount(){
        this.setState({priceConst: this.props.item.price,
                       sumPrice: this.props.item.price});
    }

    incSum = ()=>{
        this.setState((state)=>{
            const { priceConst } = state;
            let massa = state.massa+100;
            if(massa>1000){massa=1000}
            return( {sumPrice: priceConst*(massa/100), 
                     massa} )
        })
    }
        
    decSum = ()=>{
        this.setState((state)=>{
            const { priceConst } = state;
            let massa = state.massa-100;
            if(massa<100){massa=100}
            return( {sumPrice: priceConst*(massa/100), 
                     massa} )
        })
    }
	
	makeItemForBasket = (item, massa)=>{
        console.log(item);
		return{id: item.id, title: item.title, massa, price: item.price, image: item.imageUrl, country: item.country, category: item.categoru}
    };
    
    toggleTextButton = (evt) => {
        const el =  evt.target;
        el.classList.add('tttt');
        const text = evt.target.innerHTML;
        evt.target.innerHTML = "ДОБАВЛЕНО";
        setTimeout(()=>{el.innerHTML=text; el.classList.remove('tttt')}, 1000);
      }

    render(){
        const {title, categoru, country, description, imageUrl} = this.props.item;
        const {sumPrice, massa} = this.state;
        return(
            <div className="detail-main">
                <div className="image">
                    <img src={imageUrl} alt="detail-tea" />
                </div>
                <div className="discription">
                    <div>
                        <div className="title">{title}</div>
                        <div className="category">{categoru}</div>
                        <div className="country">{country}</div>
                        <div className="price">{sumPrice} ₽</div>
                        <div className="add-tea-block">
                        <div className="inc-dec">
                            <div className="minus" onClick={this.decSum}><p>-</p></div>
                            <div className="massa"><p>{massa} гр.</p></div>
                            <div className="plus" onClick={this.incSum}><p>+</p></div>
                        </div>
                        <button className="add-basket" 
                                onClick={(evt)=>{
                                    this.props.addToBasket(this.makeItemForBasket(this.props.item, massa)); 
                                    this.toggleTextButton(evt)}}>
                            В КОРЗИНУ
                        </button>
                    </div>
                        <div className="discription-text">{description}</div>
                    </div>
                    <DetailMore item={this.props.item} />
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => {//тестовый функционал, нужно почистить 
	return{data: state}
}

const mapDispatchToProps = (dispatch) => {
	return{addToBasket: (id, massa)=>{dispatch(addToBasket(id, massa))}}
}

export default connect(mapStateToProps, mapDispatchToProps)(DetailMain);