import React from 'react';
import './footer.css';

import {Link} from 'react-router-dom';

import {ReactComponent as Facebook} from '../svg/facebook.svg';
import {ReactComponent as Instagram} from '../svg/instagram.svg';
import {ReactComponent as Twitter} from '../svg/twitter.svg';
import {ReactComponent as Youtube} from '../svg/youtube.svg';

const Footer = () => {
    return(
    <footer className="footer">
        <div className="footer__container">
            <div className="footer__contact-block">
                <p className="footer__address">123456, г. Москва, ул. Уличная, д. 1, корп. 1</p>
                <div className="footer__phone-mail">
                    <p>+7 495 000 00 00</p>
                    <p>info@cup-tea.ru</p>
                </div>
            </div>
            <div className="footer__logo-block">
                <Link to="/" className="footer__logo">Cup-Tea</Link>
            </div>
            <div className="footer__social-block">
            <p className="footer__social-text">Интернте магазин самого вкусного и полезноо чая</p>
            <div className="footer__social-web">
                    <div><Facebook /></div>
                    <div><Instagram /></div>
                    <div><Twitter /></div>
                    <div><Youtube /></div>
                </div>
            </div>
        </div>
        <div className="footer__copyright">
            <p>2020. CUP-TEA. Интернет-магазин самого вкусного и полезного чая.</p>
        </div>
    </footer>
    );
}

export default Footer;