import React, {Component} from 'react';
import FilterListItem from './filter-list-item';
import './filter-list.css';

class FilterList extends Component{
    constructor(){
        super();
        this.state = {
            view: false
        };
    };
    
    isToggleView = ()=>{
        this.setState((state)=>{
            return{view: !state.view};
        });
    };
    
    render(){
        const { update, config } = this.props;
        const toggleClass= this.state.view?'hide':'filter-block-items';
        
        const elements = config.list.map((item, id)=>{
            return <div key={id}><FilterListItem item={item} update={()=>update(id)} /></div>
        });

    return(
        <div className="filter-list">
            <div className="filter-header" onClick={this.isToggleView}>
                <p>{config.name}</p>
            </div>
            <div className={`filter-block-items ${toggleClass}`}>
                {elements}
            </div>
        </div>
    );}
};

export default FilterList;