import React from 'react'; 
import {connect} from 'react-redux';
import PaginatorItem from './paginator-item';
import {setPage} from '../../actions';
import './paginator.css';

const Paginator = (props) => {
    
    const pageCount = (arr)=>{
        if (arr.length===0){return 0};
        return Math.ceil(arr.length/6);
    };
    
    const pages = (num, page)=>{
        const x = [];
        if(num<1){return x};
        for(let i=1; i<=num; i++){
            const element = <div key={i}><PaginatorItem num={i} setPage={props.setPage} page={props.page}/></div>;
            x.push(element);
        };
        return x;
    };
    
    const arr = pages(pageCount(props.view), props.page);
    
    
    return(
       <div className="paginator">
        {arr}
       </div>
    );
};

const mapStateToProps = (state)=>{
    return{view: state.view, page: state.page}
};

const mapDispatchToProps = (dispatch)=>{
    return {setPage: (num)=>{dispatch(setPage(num))}};
};

export default connect(mapStateToProps, mapDispatchToProps)(Paginator);