import React, {Component} from 'react';
import {connect} from 'react-redux';
import {sortUp, sortDown} from '../../actions';
import './sorting.css';

class Sorting extends Component{
    constructor(){
        super();
        this.state={//можно удалять, состояние не нужно 
            isViewItemsSort: false,
        }
    }
   
    isToggleViewItemsSort = () => {//можно удалять
        this.setState((state)=>{
            const isViewItemsSort = state.isViewItemsSort?false:true;
            return{isViewItemsSort}
        })
    }
    
    isToggleState = ()=>{//можно удалять 
        this.setState((state)=>{
            return{isViewItemsSort: !state.isViewItemsSort}
        });
    }

    isToggleViewItemsSort1 = () =>{
        document.querySelector('.items-sort').classList.toggle('sort-toggle');
    }
    
    render(){
    const style = {display: this.state.isViewItemsSort?'block':'block'};//всегда открыт 
    const text = this.props.sort?"СОРТИРОВКА: цена ↑":"СОРТИРОВКА: цена ↓";
    return(
    <div className="sorting">
      <div className="sort-box">
        <div className="main-sort-row" onClick={this.isToggleViewItemsSort1}>{text}</div>
        <div className="items-sort" style={style}>
          <div className="item-sort" onClick={()=>{this.props.sortUp(); this.isToggleState()}}>цена ↑</div>
          <div className="item-sort" onClick={()=>{this.props.sortDown(); this.isToggleState()}}>цена ↓</div>
        </div>
      </div>
    </div>
    );}
}

const mapStateToProps = (state)=>{
    return{sort: state.sortUp}
}

const mapDispatchToProps = (dispatch) => {
    return{
        sortUp: ()=>{dispatch(sortUp())},
        sortDown: ()=>{dispatch(sortDown())}
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(Sorting);