import React from 'react';
import DetailMoreItem from './detail-more-item';
import './detail-more.css';

const DetailMore = (props)=>{
    const {gramm, time, temperature} = props.item;
    return(
        <div className="detail-more">
            <div className="head-text">Рекомендации по завариванию</div>
                <div className="item">
                    <DetailMoreItem value={`${gramm} гр. на чашку 200мл.`} text='Количество:'/>
                    <DetailMoreItem value={`${time} мин.`} text='Время:'/>
                    <DetailMoreItem value={`${temperature} C°`} text='Температура:'/>
                </div>
        </div>
    );
}

export default DetailMore;