import React from 'react';
import './slider.css';
import 'slick-slider/slick/slick.css';
import $ from 'jquery';
import 'slick-slider/slick/slick';

import {ReactComponent as NextArrow} from '../svg/next.svg';
import {ReactComponent as PrevArrow} from '../svg/prev.svg';

class Slider extends React.Component{

componentDidMount(){
  $('.slider').slick({
    prevArrow: ".slider-block .prev-arrow",
    nextArrow: ".slider-block .next-arrow",
    autoplay: true, 
    autoplaySpeed: 3000,
    speed: 1000    
  });
}

render(){
  return(
    <div className="slider-block">
      <button className="next-arrow">
        <NextArrow className="next-icon" />
      </button>
      <button className="prev-arrow">
        <PrevArrow className="prev-icon" />
      </button>
    <div className="slider">
      <div>
        <img src="https://artlabar.github.io/cup-tea/img/slide1.jpg" alt="tea" />
      </div>
      <div>
        <img src="https://artlabar.github.io/cup-tea/img/slide2.jpg" alt="tea" />
      </div>
      <div>
        <img src="https://artlabar.github.io/cup-tea/img/slide3.jpg" alt="tea" />
      </div>
      <div>
        <img src="https://artlabar.github.io/cup-tea/img/slide4.jpg" alt="tea" />
      </div>
    </div>
    </div>
    );
}


}

export default Slider;