import React from 'react';
import './paginator-item.css';

const PaginatorItem = (props) => {
        let classPage = null;
        if(props.page===props.num){classPage='class_page'}
    return(
        <div className={`paginator-item ${classPage}`} onClick={()=>{props.setPage(props.num); window.scrollTo(0, 0);}}>
            <p>{props.num}</p>
        </div>
    );
};

export default PaginatorItem;