import React from 'react';
import {Link} from 'react-router-dom';
import './total-order.css'

const TotalOrder = (props) => {
    return(
        <div className="total-order">
            <div className="total-order__block">
                <p className="total-order__text">Итого к оплате:</p>
                <p className="total-order__price">{props.total} руб</p>
            </div>
            <Link to="/checkout"><button>ПЕРЕЙТИ К ОФОРМЛЕНИЮ</button></Link>
        </div>
    );
};

export default TotalOrder;