import React from 'react';
import './basket-item.css';

import {ReactComponent as Delete} from '../svg/delete.svg';

const BasketItem = (props) => {
    const {title, massa, price, image, id, country, category} = props.item;
    const sum = massa/100*price;

    return(
        <div className="basket-item">
            <div className="photo-name">
                <div className="image-x">
                    <img src={image} alt="tea"/>
                </div>
                <div className="title-block">
                    <div className="bi-title">{title}</div>
                    <div className="bi-catcon">{category} - {country}</div>
                </div>
            </div>
            <div className="price-count">
                    <div className="price">
                        <p>{price} руб</p>
                    </div>
                    <div className="buttons">
                        <div className="inc-dec">
                            <div className="m" onClick={()=>{props.decBasketItem(id)}}><p>-</p></div>
                            <div className="value"><p>{massa} гр</p></div>
                            <div className="p" onClick={()=>{props.incBasketItem(id)}}><p>+</p></div>
                        </div>
                    </div>
                    <div className="sum">
                        <p>{sum} руб</p>
                    </div>
                    <div className="del" onClick={()=>{props.removeBasketItem(id)}}>
                        <div><Delete /></div>
                    </div>
            </div>
        </div>
    );
}

export default BasketItem;